  transform: translate(0);
}

.header__button-container.anim-show {
  opacity: 0;
  transition: transform .5s cubic-bezier(0, .98, .79, .97) 1s, opacity .5s cubic-bezier(0, .98, .79, .97) 1s;
  transform: translate(0, 120%);
}

