.top-bar {
  -ms-flex-pack: justify;
  justify-content: space-between;
  -ms-flex-align: center;
  align-items: center;
  padding-top: 20px;
  display: -ms-flexbox;
  display: flex;
  position: relative;
}

.top-bar__container {
