}

.top-bar__menu {
  color: #fff;
  transform-origin: top;
  z-index: 1;
  background-color: #2c2c2c;
  width: 100%;
  transition-property: transform;
  transition-duration: .5s;
  transition-timing-function: cubic-bezier(0, .98, .79, .97);
  position: absolute;
  top: 100%;
  transform: scaleY(0);
}

@media (min-width: 1024px) {
  .top-bar__menu {
    width: -moz-fit-content;
    width: fit-content;
    display: block;
    position: static;
    transform: none;
  }
}

.top-bar__menu.open-menu {
  transform: scaleY(1);
}

.top-bar__mobile-menu {
  cursor: pointer;
  background-color: #2c2c2c;
  border: none;
  width: 24px;
  height: 24px;
  margin: 0;
  padding: 0;
}

@media (min-width: 1024px) {
