}

.top-bar__icon-close {
  display: none;
}

.top-bar__icons {
  width: 100%;
  height: 100%;
  transition-property: transform;
  transition-duration: .5s;
  transition-timing-function: cubic-bezier(0, .98, .79, .97);
}

.top-bar__icons:hover {
  transform: scale(1.1);
}

.top-bar__icons.open-menu .top-bar__icon-menu {
  display: none;
}

.top-bar__icons.open-menu .top-bar__icon-close {
  display: block;
}

